<template>
  <section :class="{recommendContainer: 1,recommendLogin: recommendLogin}">
    <transition enter-active-class="fadeIn" leave-active-class="fadeOut">
      <login v-show="!LOGIN_USER_OBJECT && showLoginModal" v-on:showDialogEvent="showDialogEvent"></login>
    </transition>

    <work-area-item v-if="WORK_RANGE_COMM_OBJECT.WORK_COMM_LIST.length > 0" :areaList="WORK_RANGE_COMM_OBJECT.WORK_COMM_LIST"></work-area-item>

    <div v-if="!WORK_RANGE_COMM_OBJECT.WORK_COMM_LIST">
      <img src="../../assets/img/jeph52zp.png">
    </div>
    <div style="padding: 10px 0 60px 0;text-align: center;background-color: #fff;color: #777;" v-if="falgNoData">没有更多了~</div>
  </section>
</template>
<script>
  import login from '@/components/login'
  import workAreaItem from '@/components/workAreaItem'
  import Vue from 'vue'
  import VueLazyload from 'vue-lazyload'
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('workRangeModule')
  
  Vue.use(VueLazyload, {
    error: require('../../assets/img/default_blue.png'),
    loading: require('../../assets/img/Loading4.gif')
  })

  export default {
    components: {
      login,
      workAreaItem
    },
    computed: {
    },
    data() {
      return {
        falgNoData: false,
        page: 1,
        pagination: true,
        showData: false,
        showLoginModal: 0,
        recommendLogin: 0
      }
    },
    methods: {
      ...mapActions([
        'WORK_RANGE_COMM'
      ]),
      enterWorkDetail(item){
        this.$router.push({
          name: 'workRangeDetail',
          params: {
            taskId: item.taskId,
            jobcompletionId: item.jobcompletionId
          }
        })
      },
      // 子级组件自定义事件
      showDialogEvent(data) {
        this.showLoginModal = data;
      },
      // 点赞操作【获取当前将要点击作品的点击状态，根据点击状态请求不同的接口，未登录则不允许操作】
      __parise(item) {

        if(!this.LOGIN_USER_OBJECT){
          this.showLoginModal = 1
          return
        }

        this.axios.post('/api/jobsList/parise', {
          jobcompletionId: item.jobcompletionId,
          action: item.isPraise === 'Y' ? 'NORMAL' : 'UPVOTE'
        }).then(data => {
          this.$set(item, 'isPraise', data.data.status)
          if(data.data.status === 'Y'){
            this.$Toast({
              message: '点赞成功',
              position: 'middle',
              duration: 1000
            })
            this.$set(item, 'praiseNumber', ++item.praiseNumber)
          }else{
            this.$Toast({
              message: '取消点赞',
              position: 'middle',
              duration: 1000
            })
            this.$set(item, 'praiseNumber', --item.praiseNumber)
          }

          for (const key in this.$store.state.workRangeModule) {
            if(key !== 'WORK_RANGE_COMM_OBJECT'){
              for (const item in this.$store.state.workRangeModule[key]) {
                if(item.indexOf('PAGE') > -1){
                  this.$store.state.workRangeModule[key][item] = 1
                }else{
                  this.$store.state.workRangeModule[key][item] = []
                }
              }
            }
          }
          
        }).catch(err => {
          console.log(err)
        })
      },
      // 手势上滑加载数据
      dropDown() {
        if (this.falgNoData) { //数据全部加载完了
          return;
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
            this.loading()
            this.falgNoData = true
            this.WORK_RANGE_COMM({
                page: ++this.$store.state.workRangeModule.WORK_RANGE_COMM_OBJECT.WORK_COMM_PAGE,
                categorieId: this.$route.params.id
              }).then(res => {
              this.hideLoading()
              if (this.pagination) {
                if (res.data.list.length > 0) {
                  this.falgNoData = false;
                } else {
                  this.$Toast({
                    message: '没有更多了~',
                    position: 'middle',
                    duration: 1000
                  })
                  this.pagination = false
                  this.showData = true
                  this.falgNoData = true
                }
              }
            }).catch( err => {
              console.log(err)
            })
        }
      },
    },
    destroyed () {
      window.onscroll = () => {
        return
      }
    },
    mounted () {

      window.onscroll = () => {
        this.dropDown()
      }

      setTimeout(() => {
        document.documentElement.scrollTop = this.workRangeSavePosition
      }, 100)

      // 页面加载的等待状态
      let params = {
        page: this.WORK_RANGE_COMM_OBJECT.WORK_COMM_PAGE,
        categorieId: this.$route.params.id
      }
      if(!this.WORK_RANGE_COMM_OBJECT.WORK_COMM_LIST.length){
        this.$Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        })
        this.WORK_RANGE_COMM( params ).then(data => {
          this.$Indicator.close()
          window.onscroll = () => {
            this.dropDown()
          }
        }).catch( err => {
          console.log(err)
        })
      }
    },
    computed: {
      ...mapState([
        'WORK_RANGE_COMM_OBJECT'
      ]),
      LOGIN_USER_OBJECT(){
        return this.$store.state.userModule.LOGIN_USER_OBJECT
      },
      workRangeSavePosition(){
        return this.$store.state.workRangeSavePosition.latestTime
      }
    },
    watch: {
      '$route'(to, from){
        this.$Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        })
        
        this.$store.state.workRangeModule.WORK_RANGE_COMM_OBJECT.WORK_COMM_PAGE = 1
        this.$store.state.workRangeModule.WORK_RANGE_COMM_OBJECT.WORK_COMM_LIST = []
        let params = {
          page: this.$store.state.workRangeModule.WORK_RANGE_COMM_OBJECT.WORK_COMM_PAGE,
          categorieId: this.$route.params.id
        }
        this.WORK_RANGE_COMM( params ).then( data => {
          this.$Indicator.close()
          window.onscroll = () => {
            this.dropDown()
          }
        }).catch( err => {
          console.log(err)
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  @import url('../../assets/css/workArea');
</style>
<style>
  .emoji-convert>img {
    width: 40px;
    height: 40px;
    vertical-align: middle;
  }
</style>



